.posts{
    background-color:white;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 10px;
}
.post__header{
    display: flex;
    margin-bottom: 10px;
}
.post__info{
    margin-left: 10px;
}
.post__info > p{
    color: gray;
    font-size:12px;
}
.post__body{
    overflow-wrap: anywhere;
}
.post__body > h2{
    font-size:   15px;
}
.post__button{
    display: flex;
    justify-content: space-evenly;
}